import Main from "../../Main";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
const fnSave = async () => {
  let url = "/rest/platform/create";
  await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      pfName: document.querySelector("#pfName").value,
      pfStartDate: document.querySelector("#pfStartDate").value,
      pfEndDate: document.querySelector("#pfEndDate").value,
    }),
  }).then((res) => console.log(res));
};

const ModuleForm = () => {
  return (
    <Main title="" description="">
      <Row>
        <Form.Group className="form-box">
          <Form.Label htmlFor="pfName">Platform Name</Form.Label>
          <Form.Control
            type="text"
            id="pfName"
            aria-describedby="pfNameDesc"
            required
          />
          <Form.Text id="pfNameDesc" muted>
            등록할 플랫폼 이름을 입력해주세요.
          </Form.Text>
        </Form.Group>
      </Row>
      <Row>
        <Col md>
          <Form.Group className="form-box">
            <Form.Label htmlFor="pfStartDate">Start Date</Form.Label>
            <Form.Control
              type="date"
              id="pfStartDate"
              aria-describedby="pfStartDateDesc"
              required
            />
            <Form.Text id="pfStartDateDesc" muted>
              시작일을 입력해주세요.
            </Form.Text>
          </Form.Group>
        </Col>
        <Col md>
          <Form.Group className="form-box">
            <Form.Label htmlFor="platformEndDate">End Date</Form.Label>
            <Form.Control
              type="date"
              id="pfEndDate"
              aria-describedby="endDateDesc"
              required
            />
            <Form.Text id="endDateDesc" muted>
              종료일을 입력해주세요.
            </Form.Text>
          </Form.Group>
        </Col>
      </Row>

      <Row className="mt-3">
        <Button className="btn-primary" onClick={fnSave}>
          등록
        </Button>
      </Row>
    </Main>
  );
};

export default ModuleForm;
